html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

html,
body {
  height: 100%;
  font-family: Roboto, sans-serif;
  font-weight: 400;
  font-size: 12px;
}

.content {
  min-height: 100%;
  display: flex;
  flex-direction: column;
}

